.subpage-menu {
  background-color: #3c8d2f;
  padding: 10px;
}

.subpage-link {
  background-color: #3c8d2f;
  padding: 10px;
  color: white;
  text-decoration: none;
  box-sizing: border-box;
}

.subpage-link:hover {
  background-color: #96d38d;
}

.subpage-link:focus,
a.is-active {
  background-color: #64b058;
}
